import React from 'react';
import {
  Text, Button, Modal, ModalOverlay, ModalContent, ModalHeader, ModalBody, ModalFooter, ModalCloseButton
} from '@chakra-ui/react';

import style from './ConfirmModal.module.css';

interface Props {
  showModal: boolean,
  title: string,
  description: string,
  cancelButton?: string,
  submitButton?: string,
  onCancel?: (arg: any) => void;
  onOk?:() => void;
}

const ConfirmModal = ({
  title, description, cancelButton, submitButton, showModal, onCancel, onOk
}: Props) => (
  <Modal isOpen={showModal} size="sm" isCentered onClose={() => onCancel}>
    <ModalOverlay />
    <ModalContent className={`${style.modal}`}>
      <ModalHeader className={`${style.modalHeader}`}>{title}</ModalHeader>
      <ModalCloseButton onClick={onCancel} className={`${style.modalClose}`} />
      <ModalBody className={`${style.modalBody}`}>
        <Text>{description}</Text>
      </ModalBody>

      <ModalFooter>
        <Button colorScheme="branding" variant="outline" mr={3} onClick={onCancel}>
          {cancelButton || 'Cancel'}
        </Button>
        <Button onClick={onOk} variant="primary">{submitButton || 'Submit'}</Button>
      </ModalFooter>
    </ModalContent>
  </Modal>
);

export default ConfirmModal;
