/* eslint-disable no-constant-condition */
import React, {
  useCallback, useEffect, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, Switch, useBoolean, useToast
} from '@chakra-ui/react';
import Icon from '../../../components/Icon';
import style from './CardDetails.module.css';
import ConfirmModal from '../../../components/ConfirmModal/ConfirmModal';
import { useAppAsyncDispatch } from '../../../hooks/store';
import { cardDelete, getCard, updateCard } from '../../../store/middlewares/card';
import { card, cardPayload } from '../../../types/card';
import { apiErrorHandler } from '../../../utils/general';
import Card from '../../../components/Card';
import { updateCardPayload, updateReplaceCardId, updateSelectedCard } from '../../../store/actionCreators/card';
import MiniBox from '../../../layout/MiniBox';
import { useDrawer } from '../../../hooks/drawer';

const CardEditInfo = React.lazy(() => import('../Edit/CardInfo/CardInfo'));
const AtmInfo = React.lazy(() => import('../AtmInfo/AtmInfo'));

const CardDetails = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const toast = useToast();
  const { open, close, data } = useDrawer<{id: string}>();
  const id = data?.id || '';
  const [cardData, updateCardData] = useState<card | null>(null);
  // const [activeCard, setActiveCard] = useState(false);
  const [isFreezed, freezeAction] = useBoolean();
  const [freezeModal, setFreezeModal] = useState(false);
  const [unfreezeModal, setUnFreezeModal] = useState(false);
  const [cancelCard, setCancelCard] = useState(false);
  const [replaceCard, setReplaceCard] = useState(false);

  const numberRef = useRef<HTMLDivElement>(null);
  const copyBtnRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (id) {
      dispatch(getCard(id))
        .then((c) => {
          updateCardData(c);
          dispatch(updateSelectedCard(c));
          if (c.cardStatus === 'inactive') {
            freezeAction.on();
          }
        })
        .catch(apiErrorHandler);
    }
  }, [id]);

  const toggleFreeze = (checked: any) => {
    if (checked) {
      setFreezeModal(true);
    } else {
      setUnFreezeModal(true);
    }
    freezeAction.toggle();
  };

  const freezeUnfreeze = useCallback(() => {
    if (cardData?.id) {
      dispatch(updateCard(cardData.id, {
        cardStatus: isFreezed ? 'inactive' : 'active'
      }))
        .then((c) => {
          updateCardData(c);
          dispatch(updateSelectedCard(c));
        }).catch((e) => {
          freezeAction.toggle();
          apiErrorHandler(e);
        })
        .finally(() => {
          setFreezeModal(false);
          setUnFreezeModal(false);
        });
    }
  }, [cardData?.id, isFreezed]);

  const cancelCardAction = useCallback(() => {
    if (cardData?.id) {
      setCancelCard(false);
      dispatch(cardDelete(cardData.id))
        .then(() => {

        }).catch(apiErrorHandler);
    }
  }, [cardData]);

  const goToTransactions = useCallback(() => {
    if (cardData) {
      console.log('cardData');
    }
  }, [cardData]);

  type Headers = {
    'sd-show-token': string;
  };

  const renderCardNumber = useCallback(({ show, headers }: {
    show:any, headers: Headers
  }) => {
    if (cardData) {
    /**
       * card number
       */
      const cardNoiframe = show.request({
        name: 'cardNoIframe2',
        method: 'GET',
        headers,
        path: `/v1/card/${cardData.id}/show`,
        jsonPathSelector: 'cardNumber',
        serializers: [show.SERIALIZERS.replace('(\\d{4})(\\d{4})(\\d{4})(\\d{4})', '$1 $2 $3 $4')],
      });
      cardNoiframe.render(numberRef.current, {
        color: '#767676', fontSize: '16px', display: 'block', height: '16px', fontWeight: 500, letterSpacing: 0.5, lineHeight: '24px'
      });

      const copyButton = show.copyFrom(cardNoiframe, { text: '<svg width="13" height="13" viewBox="0 0 30 30" style=""><g transform="scale(0.03125 0.03125)"><path d="M373.336 832c-99.969 0-181.336-81.374-181.336-181.338v-437.328h-74.664c-64.726 0-117.336 52.601-117.336 117.328v575.997c0 64.734 52.61 117.342 117.336 117.342h533.331c64.725 0 117.333-52.608 117.333-117.342v-74.658h-394.664z"></path><path d="M938.667 117.336c0-64.812-52.523-117.336-117.333-117.336h-447.997c-64.812 0-117.336 52.524-117.336 117.336v533.328c0 64.813 52.524 117.336 117.336 117.336h447.997c64.811 0 117.333-52.523 117.333-117.336v-533.328z"></path></g></svg>' }, (status: string) => {
        if (status === 'success') {
          toast({
            title: 'Card Number copied.',
            status: 'success',
            duration: 9000,
            isClosable: true,
          });
        }
      });

      copyButton.render(copyBtnRef.current, {
        cursor: 'pointer',
        overflow: 'hidden',
        left: 'auto',
        right: '0',
        width: 'auto',
        fill: getComputedStyle(document.documentElement).getPropertyValue('--icon-color')
      });
    }
  }, [cardData]);

  if (cardData === null) {
    return null;
  }

  return (
    <Box className="page">
      <MiniBox
        showBack
        onBack={() => {
          close();
        }}
        heading={t('card_details.title')}
      >
        <Box width="100%">

          {/* Cards */}
          <Card
            key={cardData.id}
            data={cardData}
            showSecrets
            onReady={renderCardNumber}
          />
          {/* Card options */}

          <Box className={`${style.cardDetails__action_in_list} ${style.cardDetails__cardNo}`}>
            <Text variant="regular" pr="2px" textAlign="left">{t('card_details.cardNo')}</Text>
            <Text className={`${style.cardNumber} cardIframeWrapper cardIframeWrapper__number`} ref={numberRef} variant="regular text-light" />
            <span className={`${style.iconCopy} cardIframeWrapper cardIframeWrapper__copy`} ref={copyBtnRef} />
          </Box>

          <Box onClick={goToTransactions} className={`${style.cardDetails__action_in_list} ${style.pointer}`}>
            <Text variant="regular" mr="auto" textAlign="left">{t('card_details.transactions')}</Text>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </Box>

          <Text variant="large" pt="20px" pb="16px">{t('card_details.actions')}</Text>
          <Box rounded="4px" pb="20px">

            <Box className={style.cardDetails__action_in_list}>
              <Text variant="regular" mr="auto" textAlign="left">{t('card_details.freezeCard')}</Text>
              <Switch variant="primary" id="send-by-email" isChecked={isFreezed} onChange={(e:any) => toggleFreeze(e.target.checked)} />
            </Box>

            <Box className={style.cardDetails__action_in_list}>
              <Text variant="regular" textAlign="left">
                {t('card_details.cardLabel')}
                :
              </Text>
              <Text variant="regular" pl="8px" mr="auto" className="text-light">{cardData.label}</Text>
              <Text variant="small" className={`${style.pointer} text-dark`} fontSize="14px" textDecor="underline" onClick={() => open(CardEditInfo)}>{t('card_details.edit')}</Text>
            </Box>

            <Box className={style.cardDetails__action_in_list}>
              <Text variant="regular" textAlign="left">{t('card_details.spendingLimit')}</Text>
              <Text variant="regular" color="#2EC193" pl="8px" mr="auto" textAlign="left">
                $
                {' '}
                {cardData.limitAmount}
              </Text>
              <Text variant="small" className={`${style.pointer} text-dark`} fontSize="14px" textDecor="underline" onClick={() => open(CardEditInfo)}>{t('card_details.edit')}</Text>
            </Box>

            {cardData.cardStatus === 'active' ? (
              <Box className={`${style.cardDetails__action_in_list} ${style.pointer}`} onClick={() => open(AtmInfo)}>
                <Text variant="regular" mr="auto" textAlign="left">{t('card_details.atmInfo')}</Text>
                <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
              </Box>
            ) : null}

            <Box className={`${style.cardDetails__action_in_list} ${style.pointer}`} onClick={() => setCancelCard(true)}>
              <Text variant="regular" mr="auto" textAlign="left">{t('card_details.cancelCard')}</Text>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </Box>

            <Box
              className={`${style.cardDetails__action_in_list} ${style.pointer}`}
              onClick={() => {

              }}
            >
              <Text variant="regular" mr="auto" textAlign="left">{t('card_details.getHelp')}</Text>
              <Text variant="small" fontSize="14px" textDecor="underline" className={`${style.pointer} text-dark`}>{t('card_details.chat')}</Text>
            </Box>

          </Box>
        </Box>

        {/* Modals */}
        {/*
        TODO: Fix cancel button
      */}
        <ConfirmModal
          showModal={freezeModal}
          submitButton={t('card_details.cardInfo_freeze_button')}
          cancelButton={t('card_details.cancel')}
          title={t('card_details.freezeCard')}
          description={t('card_details.freezeDescription')}
          onCancel={() => {
            setFreezeModal(false);
            freezeAction.toggle();
          }}
          onOk={freezeUnfreeze}
        />

        <ConfirmModal
          showModal={unfreezeModal}
          submitButton={t('card_details.cardInfo_unfreeze_button')}
          cancelButton={t('card_details.cancel')}
          title={t('card_details.unFreezeCard')}
          description={t('card_details.unFreezeDescription')}
          onCancel={() => {
            setUnFreezeModal(false);
            setFreezeModal(false);
            freezeAction.toggle();
          }}
          onOk={freezeUnfreeze}
        />

        <ConfirmModal showModal={cancelCard} submitButton={t('card_details.yes')} cancelButton={t('card_details.no')} title={t('card_details.cancelCard')} description={t('card_details.cardInfo_cancel_alert_messsage')} onCancel={() => setCancelCard(false)} onOk={cancelCardAction} />

        <ConfirmModal
          showModal={replaceCard}
          submitButton={t('card_details.yes')}
          cancelButton={t('card_details.no')}
          title={t('card_details.replaceCard')}
          description="Replacing your card will discard your exisiting card information and a new card will be issued with a different card number"
          onOk={() => {
            dispatch(updateReplaceCardId(cardData.id));
            setReplaceCard(false);
            dispatch(updateCardPayload({ cardType: cardData.cardType, accountId: cardData.accountId, currency: 'USD' } as cardPayload));
            setTimeout(() => {
              open(CardEditInfo);
            }, 0);
          }}
          onCancel={() => {
            setReplaceCard(false);
          }}
        />
      </MiniBox>

    </Box>
  );
};

export default CardDetails;
